import React from "react";
import { atualidadeText } from "../../resources/texts";
import taipe from "../../resources/images/taipe.png";
import tsmc from "../../resources/images/tsmc.jpeg";

export const VIIPageTwo = props => {

    return (
        <>
            <h1 className='stageTitle'>Taipé</h1>
            <section className='grid2x2'>
                <p className='stageParagraph leftStack'>{atualidadeText.pageTwo}</p>
                <figure className='stageFigure iTopRight'>
                    <img src={taipe}/>
                    <figcaption>Taipé atualemte</figcaption>
                </figure>
                <figure className='stageFigure iBottomRight'>
                    <img src={tsmc}/>
                    <figcaption>TSMC, grande fabricante de chips de computador</figcaption>
                </figure>
            </section>
        </>
    );
};