import React from "react";
import { guerraFriaText } from "../../resources/texts";
import warsaw from "../../resources/images/warsaw.png";
import apartments from "../../resources/images/apartments.jpg";

export const VIPageOne = props => {

    return (
        <>
            <h1 className='stageTitle'>VI. Guerra Fria</h1>
            <section className='grid2x2'>
                <p className='stageParagraph leftStack'>{guerraFriaText.pageOne}</p>
                <figure className='stageFigure iTopRight'>
                    <img src={warsaw}/>
                    <figcaption>Pacto de Varsóvia</figcaption>
                </figure>
                <figure className='stageFigure iBottomRight'>
                    <img src={apartments}/>
                    <figcaption>Apartamentos estilo soviético</figcaption>
                </figure>
            </section>
        </>
    );
};