import React from "react";
import { guerraFriaText } from "../../resources/texts";
import wall from "../../resources/images/wall.jpg";
import germany from "../../resources/images/germany.png";

export const VIPageTwo = props => {

    return (
        <>
            <h1 className='stageTitle'>Berlim</h1>
            <section className='grid2x2'>
                <p className='stageParagraph doubleBottom' >{guerraFriaText.pageTwo}</p>
                <figure className='stageFigure iTopLeft'>
                    <img src={germany}/>
                    <figcaption>Alemanha Ocidental e Oriental</figcaption>
                </figure>
                <figure className='stageFigure iTopRight'>
                    <img src={wall}/>
                    <figcaption>Restos do Muro de Berlim</figcaption>
                </figure>
            </section>
            
        </>
    );
};